// src/components/DSA/ChapterList.js
"use client";

import { useState, useEffect } from "react";
import {
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Typography,
  Box,
  CircularProgress,
  Divider,
  Chip,
  useTheme,
  alpha,
} from "@mui/material";
import {
  CheckCircle as CheckCircleIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  Code as CodeIcon,
} from "@mui/icons-material";
import { dsaService } from "@/services/dsaService";

export default function ChapterList({
  topics = [],
  selectedTopic,
  onSelectTopic,
  completedSubtopicIds = [],
}) {
  const theme = useTheme();
  const [loading, setLoading] = useState(true);
  const [expandedTopic, setExpandedTopic] = useState(null);

  const handleTopicClick = (topic) => {
    if (expandedTopic === topic._id) {
      setExpandedTopic(null);
    } else {
      setExpandedTopic(topic._id);
      if (onSelectTopic) {
        onSelectTopic(topic);
      }
    }
  };

  const getCompletedCount = (topic) => {
    if (!topic.subtopics || !completedSubtopicIds) return 0;
    return topic.subtopics.filter((st) => 
      completedSubtopicIds.includes(st._id)
    ).length;
  };

  if (loading && topics.length === 0) {
    return (
      <Box sx={{ display: "flex", justifyContent: "center", p: 4 }}>
        <CircularProgress />
      </Box>
    );
  }

  if (topics.length === 0) {
    return (
      <Box sx={{ p: 3, textAlign: "center" }}>
        <Typography color="text.secondary">
          No chapters available
        </Typography>
      </Box>
    );
  }

  return (
    <List
      sx={{
        width: "100%",
        bgcolor: "background.paper",
        borderRadius: 2,
        overflow: "hidden",
      }}
    >
      {topics.map((topic) => {
        const isExpanded = expandedTopic === topic._id;
        const completedCount = getCompletedCount(topic);
        const totalCount = topic.subtopics?.length || 0;
        const progress = totalCount > 0 ? (completedCount / totalCount) * 100 : 0;

        return (
          <Box key={topic._id} sx={{ mb: 1 }}>
            <ListItem
              disablePadding
              sx={{
                bgcolor: isExpanded
                  ? alpha(theme.palette.primary.main, 0.08)
                  : "transparent",
                borderLeft: isExpanded
                  ? `4px solid ${theme.palette.primary.main}`
                  : "none",
                transition: "all 0.2s ease",
              }}
            >
              <ListItemButton
                onClick={() => handleTopicClick(topic)}
                sx={{
                  py: 1.5,
                  "&:hover": {
                    bgcolor: alpha(theme.palette.primary.main, 0.04),
                  },
                }}
              >
                <ListItemIcon sx={{ minWidth: 40 }}>
                  <CodeIcon
                    color={
                      isExpanded ? "primary" : "disabled"
                    }
                  />
                </ListItemIcon>
                <ListItemText
                  primary={
                    <Typography
                      variant="subtitle2"
                      color={isExpanded ? "primary" : "text.primary"}
                      fontWeight={isExpanded ? 600 : 400}
                    >
                      {topic.name}
                    </Typography>
                  }
                  secondary={
                    <Box
                      component="span"
                      sx={{ display: "flex", alignItems: "center", mt: 0.5 }}
                    >
                      <Typography
                        variant="caption"
                        color="text.secondary"
                        sx={{ mr: 1 }}
                      >
                        {completedCount} of {totalCount} completed
                      </Typography>
                      <Box
                        sx={{
                          flexGrow: 1,
                          height: 4,
                          bgcolor: "divider",
                          borderRadius: 2,
                          overflow: "hidden",
                          maxWidth: 100,
                        }}
                      >
                        <Box
                          sx={{
                            width: `${progress}%`,
                            height: "100%",
                            bgcolor: "primary.main",
                          }}
                        />
                      </Box>
                    </Box>
                  }
                />
                {completedCount === totalCount && totalCount > 0 && (
                  <CheckCircleIcon
                    color="success"
                    fontSize="small"
                    sx={{ ml: 1 }}
                  />
                )}
              </ListItemButton>
            </ListItem>

            {isExpanded && topic.subtopics?.length > 0 && (
              <List disablePadding>
                {topic.subtopics.map((subtopic) => {
                  const isCompleted = completedSubtopicIds.includes(subtopic._id);
                  return (
                    <ListItem
                      key={subtopic._id}
                      disablePadding
                      sx={{
                        bgcolor: "background.paper",
                        pl: 6,
                        pr: 2,
                        py: 0.5,
                        "&:hover": {
                          bgcolor: "action.hover",
                        },
                      }}
                    >
                      <ListItemButton
                        dense
                        sx={{
                          borderRadius: 1,
                          "&:hover": {
                            bgcolor: "transparent",
                          },
                        }}
                      >
                        <ListItemIcon sx={{ minWidth: 32, color: isCompleted ? "success.main" : "text.disabled" }}>
                          {isCompleted ? (
                            <CheckCircleIcon fontSize="small" />
                          ) : (
                            <RadioButtonUncheckedIcon fontSize="small" />
                          )}
                        </ListItemIcon>
                        <ListItemText
                          primary={
                            <Typography
                              variant="body2"
                              color={isCompleted ? "text.primary" : "text.secondary"}
                              sx={{
                                textDecoration: isCompleted ? "none" : "none",
                                fontWeight: isCompleted ? 500 : 400,
                              }}
                            >
                              {subtopic.name}
                            </Typography>
                          }
                          secondary={
                            subtopic.difficulty && (
                              <Chip
                                label={subtopic.difficulty}
                                size="small"
                                color={getLevelColor(subtopic.difficulty)}
                                variant="outlined"
                                sx={{
                                  mt: 0.5,
                                  height: 20,
                                  fontSize: "0.65rem",
                                }}
                              />
                            )
                          }
                        />
                      </ListItemButton>
                    </ListItem>
                  );
                })}
              </List>
            )}
            <Divider />
          </Box>
        );
      })}
    </List>
  );
}
